import { dbGenerators } from "../../../database"
import { generateChatMessages } from "../../database/generators/generateChat"
import { ChatResponseHandler } from "./chat-handlers.type"

export const defaultChatPrompt: ChatResponseHandler = async ({ inputMessage, requestId }) => {
  const content = "Bigdata is a powerful tool for analyzing large datasets."
  const interaction = dbGenerators.generateChatInteraction({
    inputMessage,
    content,
  })
  const messages = generateChatMessages({ requestId, interaction, content })
  return {
    messages: [
      messages.status("Routing your query to the appropriate data source"),
      messages.status("Searching ..."),
      messages.statusQuery(),
      ...messages.next(),
      messages.complete(),
    ],
    interaction,
  }
}
